import { BlockType, GameConst, Block } from './Grid';
import UIGame from './UIGame';

export default class UIBlock extends Laya.Script {
    constructor() {
        super();
    }

    setBlock(b: Block) {
        this.m_block = b;

    }

    m_block: Block;
    m_dragging: boolean = false;
    m_dragBeginX: number;
    m_dragBeginY: number;

    onMouseDown(event: Laya.Event): void {
        this.m_dragging = true;
        this.m_dragBeginX = event.stageX;
        this.m_dragBeginY = event.stageY;
        //console.log('onMouseDown: ', this.m_block, event);
    }

    m_blockLens = [0, 2, 2, 2, 3, 3];

    onMouseMove(event: Laya.Event): void {
        if (!this.m_dragging) {
            return;
        }
        let offsetX: number = event.stageX - this.m_dragBeginX;
        let offsetY: number = event.stageY - this.m_dragBeginY;
        let sp: Laya.Sprite = this.owner as Laya.Sprite;
        let cb:Laya.Handler=Laya.Handler.create(this,this.onMouseUp);

        //console.log(`onMouseMove offset :,${offsetX},${offsetY}`)
        if (this.m_block.type == BlockType.C2 || this.m_block.type == BlockType.H2 || this.m_block.type == BlockType.H3) {
            if (offsetX > GameConst.GridSize / 2) {
                if (this.canMovePos(this.m_block.x + this.m_blockLens[this.m_block.type], this.m_block.y)) {

                    Laya.Tween.to(this.owner, { x: sp.x + GameConst.GridSize }, 100, Laya.Ease.linearInOut,cb);
                    UIGame.instance.m_grids[this.m_block.x][this.m_block.y].block = null;
                    UIGame.instance.m_grids[this.m_block.x + this.m_blockLens[this.m_block.type]][this.m_block.y].block = this.m_block;
                    this.m_block.x++;
                    this.m_dragging=false;
                    this.m_dragBeginX = event.stageX;
                    this.m_dragBeginY = event.stageY;
                }
            }
            if (offsetX < -GameConst.GridSize / 2) {
                if (this.canMovePos(this.m_block.x - 1, this.m_block.y)) {

                    Laya.Tween.to(this.owner, { x: sp.x - GameConst.GridSize }, 100, Laya.Ease.linearInOut);
                    UIGame.instance.m_grids[this.m_block.x + this.m_blockLens[this.m_block.type]-1][this.m_block.y].block = null;
                    UIGame.instance.m_grids[this.m_block.x - 1][this.m_block.y].block = this.m_block;
                    this.m_block.x--;
                    this.m_dragging=false;
                    this.m_dragBeginX = event.stageX;
                    this.m_dragBeginY = event.stageY;
                }
            }
        } else if (this.m_block.type == BlockType.V2 || this.m_block.type == BlockType.V3) {
            if (offsetY > GameConst.GridSize / 2) {
                if (this.canMovePos(this.m_block.x, this.m_block.y + this.m_blockLens[this.m_block.type])) {

                    Laya.Tween.to(this.owner, { y: sp.y + GameConst.GridSize }, 100, Laya.Ease.linearInOut);
                    UIGame.instance.m_grids[this.m_block.x][this.m_block.y].block = null;
                    UIGame.instance.m_grids[this.m_block.x][this.m_block.y + this.m_blockLens[this.m_block.type]].block = this.m_block;
                    this.m_block.y++;
                    this.m_dragging=false;
                    this.m_dragBeginX = event.stageX;
                    this.m_dragBeginY = event.stageY;
                }
            }
            if (offsetY < -GameConst.GridSize / 2) {
                if (this.canMovePos(this.m_block.x, this.m_block.y - 1)) {

                    Laya.Tween.to(this.owner, { y: sp.y - GameConst.GridSize }, 100, Laya.Ease.linearInOut);
                    UIGame.instance.m_grids[this.m_block.x][this.m_block.y + this.m_blockLens[this.m_block.type]-1].block = null;
                    UIGame.instance.m_grids[this.m_block.x][this.m_block.y - 1].block = this.m_block;
                    this.m_block.y--;
                    this.m_dragging=false;
                    this.m_dragBeginX = event.stageX;
                    this.m_dragBeginY = event.stageY;
                }
            }
        }
    }

    onMouseUp(event: Laya.Event): void {
        //console.log('onMouseUp');
        this.m_dragging = false;
        UIGame.instance.onBlockDragEnd();
    }

    canMovePos(x: number, y: number): boolean {
        if (!this.isValidGridPos(x, y)) return false;
        if (UIGame.instance.m_grids[x][y].block != null) return false;

        return true;
    }

    isValidGridPos(x: number, y: number): boolean {
        return x >= 0 && y >= 0 && x < 6 && y < 6;
    }

}