import { ui } from '../ui/layaMaxUI';
import { Grid, Block, BlockType, GameConst, LevelConfig } from './Grid';
import UIBlock from './UIBlock';

export default class UIGame extends ui.mainUI {
    m_grids: Grid[][] = []
    m_gridSize: number = 82;
    static instance: UIGame;
    constructor() {
        super();
        UIGame.instance = this;
    }

    initGrids() {

        for (let x: number = 0; x < 6; x++) {
            this.m_grids.push([]);
            for (let y: number = 0; y < 6; y++) {
                let grid: Grid = new Grid();
                grid.x = x;
                grid.y = y;
                grid.pos = new Laya.Point(x * GameConst.GridSize, y * GameConst.GridSize);
                
                this.m_grids[x][y] = grid;
            }
        }
    }



    onEnable() {
        this.initGrids();
        //加载本地存储的关卡数据
        this.LoadStorage();
        //加载图集
        //Laya.loader.load("./res/atlas/gameui.atlas");
        //加载关卡数据
        Laya.loader.load('config/levels.json', Laya.Handler.create(this, this.onLoadConfig))

        //回退按钮
        this.btnBack.on(Laya.Event.CLICK, this, this.onClickBtnBack)
        //提示按钮
        this.btnTips.on(Laya.Event.CLICK, this, this.onClickBtnTips)
    }



    m_level: string = '1';
    LoadStorage() {
        if (!Laya.LocalStorage.support) {
            return;
        }

        let level: string = Laya.LocalStorage.getItem('level');
        if (!level) {
            level = '1';
            Laya.LocalStorage.setItem('level', level);
        }
        this.m_level = level;
    }

    m_configs: JSON;
    onLoadConfig(json: JSON) {
        this.m_configs = json;

        //加载当前关卡
        let cfg = this.m_configs[this.m_level];
        console.log(cfg);

        this.buildBoard(cfg);

    }

    clearBoard(): void {
        for(var block of this.m_blocks)
        {
            for(var grid of block.grids)
            {
                grid.block = null;
            }
        }
        this.m_blocks.clear();
        this.clearBlocks();
    }

    m_blocks: Set<Block> = new Set<Block>();
    buildBoard(cfg: LevelConfig): void {
        cfg.items.forEach(item => {
            let block: Block = new Block();
            console.log(item);
            console.log(this.m_grids);
            let grid: Grid = this.m_grids[item.x][item.y];
            block.x = item.x;
            block.y = item.y;
            block.type = item.type;
            grid.block = block;
            this.m_blocks.add(block);
            let blockType: BlockType = block.type;
            if (blockType == BlockType.C2 || blockType == BlockType.H2) {
                this.m_grids[grid.x + 1][grid.y].block = block;
            }
            else if (blockType == BlockType.H3) {
                this.m_grids[grid.x + 1][grid.y].block = block;
                this.m_grids[grid.x + 2][grid.y].block = block;
            }
            else if (blockType == BlockType.V2) {
                this.m_grids[grid.x][grid.y + 1].block = block;
            }
            else if (blockType == BlockType.V3) {
                this.m_grids[grid.x][grid.y + 1].block = block;
                this.m_grids[grid.x][grid.y + 2].block = block;
            }
        })

        this.showBlocks();
    }

    showBlocks(): void {

        for (let block of this.m_blocks) {

            let sp: Laya.Sprite = new Laya.Sprite();
            sp.loadImage(`gameui/${block.type}.png`);
            sp.pos(block.x * GameConst.GridSize, block.y * GameConst.GridSize);
            let com: UIBlock = new UIBlock();
            com.setBlock(block);
            sp.addComponentIntance(com);
            this.board.addChild(sp);
        }
    }

    clearBlocks(): void {
        this.board.removeChildren();
    }


    //滑动结束
    onBlockDragEnd(): void {

        if (this.isWin()) {
            this.onWin();
        }
    }

    //判断通关
    isWin(): boolean {
        let b: Block = this.m_grids[5][2].block
        if (b && b.type == BlockType.C2) {
            return true;
        }
        return false;
    }

    //TODO 通关弹框
    onWin(): void {

        this.onClickBtnNext();
    }


    // 回退
    onClickBtnBack(): void {

    }

    //TODO 提示
    onClickBtnTips(): void {
        //看广告，并随机删除一个块
    }

    // 下一关
    onClickBtnNext(): void {
        this.clearBoard();
        this.m_level = (Number(this.m_level)+1).toString();
        let cfg:LevelConfig=this.m_configs[this.m_level];
        this.buildBoard(cfg);
    }

}