//纯逻辑
export enum BlockType {
    None,
    C2,
    H2,
    V2,
    H3,
    V3
}

export class GameConst {
    static GridSize: number = 82;

}

export class Grid {

    x: number;
    y: number;
    pos: Laya.Point;
    _block: Block;
    get block(): Block {
        return this._block;
    }
    set block(b: Block) {
        if (b) {
            b.grids.add(this);
        } else if (this._block) {
            this._block.grids.delete(this);
        }

        this._block = b;

    }
}


export class Block {
    x: number;
    y: number;
    type: BlockType;
    grids: Set<Grid>=new Set<Grid>();
}

export class LevelConfigBlock{
    x: number;
    y: number;
    type: number;

}

export class LevelConfig {
    id: string;
    items: LevelConfigBlock[];
}
